<template>
  <div id="form">
    <div id="param">
      <el-form ref="form"
               :model="form"
               label-width="120px"
               label-position="left">
        <el-form-item label="第三方名称">
          <el-input @change="signIt"
                    v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="金额单位">
          <el-select @change="signIt"
                     v-model="form.amount_unit"
                     placeholder="请选择">
            <el-option label="元"
                       value="1"></el-option>
            <el-option label="分"
                       value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接收方式">
          <el-select @change="signIt"
                     v-model="form.method"
                     placeholder="请选择">
            <el-option label="GET"
                       value="GET"></el-option>
            <el-option label="POST"
                       value="POST"></el-option>
            <el-option label="FILE"
                       value="FILE"></el-option>
            <el-option label="XML"
                       value="XML"></el-option>
            <el-option label="XMLCDATA"
                       value="XMLCDATA"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input @change="signIt"
                    v-model="form.orderNumber"></el-input>
        </el-form-item>
        <el-form-item label="成功讯息索引">
          <el-input @change="signIt"
                    v-model="form.successKey"></el-input>
        </el-form-item>
        <el-form-item label="成功讯息值">
          <el-input @change="signIt"
                    v-model="form.successValue"></el-input>
        </el-form-item>
        <el-form-item label="签名索引">
          <el-input @change="signIt"
                    v-model="form.signKey"></el-input>
        </el-form-item>
        <el-form-item label="返回讯息">
          <el-input @change="signIt"
                    v-model="form.msg"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="flexbox">
      <div id="addparameter">
        <table cellpadding="10">
          <tr v-for="(param, idx) in params"
              :key="idx">
            <th>
              <el-button type="danger"
                         class="remove-one"
                         @click="removeParam(idx)">Del</el-button>
            </th>
            <td>
              <input type="text"
                     class="arrKey"
                     placeholder="参数索引"
                     v-model="param.label">
            </td>
          </tr>
          <tr>
            <th style="color:#C73E3A">
              輸入新參數 →
            </th>
            <td>
              <input type="text"
                     placeholder="参数索引"
                     @keyup.enter="addParam"
                     id="paramKey"
                     autocomplete="off">
            </td>
          </tr>
        </table>
        <el-button type="primary"
                   id="addOne"
                   @click="addParam">+</el-button>
      </div>
      <div id="sign">
        <el-transfer class="transfer"
                     :data="params"
                     target-order="push"
                     :titles="titles"
                     v-model="value"
                     @change="renewSign">
        </el-transfer>
        <el-button @click="orderBy(['asc', 'desc'])"
                   type="warning">Ksort</el-button>
      </div>
    </div>
    <div id="encryption">
      <table cellpadding="10">
        <tr>
          <th colspan="4">
            sign拼接方式 :
          </th>
        </tr>
        <tr>
          <td>
            <el-checkbox v-model="havekey"
                         size="large"
                         @change="isRSA">加入索引</el-checkbox>
            $key
          </td>
          <td>
            <input type="text"
                   placeholder="预设为无key值"
                   class="encodeArr"
                   v-model="midConn"
                   @input="reStr">
          </td>
          <td>
            $value
          </td>
          <td>
            <input type="text"
                   placeholder="预设为无间隔"
                   class="encodeArr"
                   v-model="lastConn"
                   @input="reStr">
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <el-checkbox v-model="checked"
                         size="large"
                         @change="isRSA">是否加入密鑰</el-checkbox>
          </td>
          <td colspan="1">
            <el-radio v-show="checked"
                      v-model="keyType"
                      label="$pu_key"
                      value='$pu_key'
                      @change="reStr">公钥</el-radio>
          </td>
          <td colspan="1">
            <el-radio v-show="checked"
                      v-model="keyType"
                      label="$pr_key"
                      value='$pr_key'
                      @change="reStr">私钥</el-radio>
          </td>
        </tr>
        <tr>
          <th colspan="1"
              v-if="checked">
            密鑰拼接：
          </th>
          <td colspan="3"
              v-if="checked">
            <input type="text"
                   @input="reStr"
                   style="width:30px;margin-left:10px;margin-right:10px"
                   v-model="keyConn">
            <input type="text"
                   @input="reStr"
                   style=" margin-left:10px;margin-right:10px"
                   v-model="keyName">
            <input type="text"
                   @input="reStr"
                   style="width:30px;margin-left:10px;margin-right:10px"
                   v-model="keyConn2">
          </td>
        </tr>
        <tr v-for="(item,idx) in format"
            :key="idx">
          <th colspan="1">
            加密方式 :
          </th>
          <td colspan="3">
            <el-select v-model="format[idx]"
                       @change="reStr">
              <el-option v-for="item in formats"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
            <el-button @click="addOne"
                       v-if="idx<1"
                       type="primary"
                       icon="el-icon-plus"
                       circle></el-button>
            <el-button @click="removeOne(idx)"
                       v-else
                       type="primary"
                       icon="el-icon-minus"
                       circle></el-button>
          </td>
        </tr>
      </table>
      <div class="textArea">
        <textarea name=""
                  id="text1"
                  cols="60"
                  rows="6"
                  :value="signParam"></textarea>
      </div>
      <div class="textArea">
        <textarea name=""
                  id="text2"
                  cols="60"
                  rows="6"></textarea>
        <i class="el-icon-document copyIcon"
           @click="copy"></i>
      </div>
    </div>
    <div class="flexbox">
      <el-button type="success"
                 @click="createPHP()"
                 :disabled="loading">生成</el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'formArea',
  data() {
    return {
      // form
      form: {
        name: '',
        amount_unit: '',
        method: '',
        orderNumber: '',
        successKey: '',
        successValue: '',
        signKey: '',
        msg: '',
      },
      // params
      params: [],
      // signTransfer
      titles: ['参数', '加密参数'],
      value: [],
      signSelect: [], // 接過來需要簽名的陣列
      signParam: '', // 字串連接示意
      // sign
      midConn: '',
      lastConn: '',
      formats: [
        { label: 'MD5', value: 'MD5' },
        { label: 'SHA', value: 'SHA' },
        { label: 'base64', value: 'base64' },
        { label: 'urlencode', value: 'urlencode' },
        { label: 'AES', value: 'AES' },
        { label: 'JSON', value: 'JSON' },
        { label: 'xmlCDATA', value: 'xmlCDATA' },
        { label: 'HTML', value: 'HTML' },
        { label: '大写', value: 'upper' },
        { label: '小写', value: 'lower' },
      ],
      keyType: '$pu_key',
      keyConn: '&',
      keyConn2: '=',
      keyName: 'key',
      format: ['MD5'],
      checked: true,
      havekey: true,
      signArr: '', // JSON
      JSONstr: '',
      loading: false,
    };
  },
  methods: {
    // left
    addParam() {
      const paramKey = document.querySelector('#paramKey');
      if (paramKey.value === '') {
        // eslint-disable-next-line
        alert('请填入参数索引/参数值');
      } else {
        const newParam = {
          key: this.params.length + 1,
          label: paramKey.value,
        };
        this.params.push(newParam);
        paramKey.value = '';
        paramKey.focus();
      }
      this.reStr();
    },
    removeParam(idx) {
      this.params.splice(idx, 1);
    },
    // right
    renewSign() {
      this.signSelect = [];
      for (let i = 0; i < this.value.length; i += 1) {
        const idx = this.value[i] - 1;
        this.signSelect.push(this.params[idx]);
      }
      this.signIt();
    },
    orderBy(orders) {
      this.signSelect = [...this.signSelect].sort((a, b) =>
        ['label', 'variable'].reduce((acc, prop, i) => {
          if (acc === 0) {
            const [p1, p2] =
              orders && orders[i] === 'desc'
                ? [b[prop], a[prop]]
                : [a[prop], b[prop]];
            // eslint-disable-next-line
            acc = p1 > p2 ? 1 : p1 < p2 ? -1 : 0;
          }
          return acc;
        }, 0));
      this.value.sort();
      this.signIt();
    },
    // bottom
    addOne() {
      this.format.push('MD5');
      this.signIt();
    },
    removeOne(idx) {
      this.format.splice(idx, 1);
      this.signIt();
    },
    copy() {
      const copyArea = document.querySelector('#text2');
      copyArea.select();
      try {
        if (document.execCommand('copy', false, null)) {
          document.execCommand('copy');
          // eslint-disable-next-line
        } else alert('複製失敗');
      } catch (err) {
        // eslint-disable-next-line
        alert('複製失敗');
      }
      copyArea.blur();
    },
    isRSA() {
      if (this.checked === false) {
        this.formats.push(
          { label: 'RSA公钥', value: 'RSApu' },
          { label: 'RSA私钥', value: 'RSApr' },
        );
      }
      if (this.checked === true) {
        this.formats.pop();
        this.formats.pop();
      }
      this.reStr();
    },
    reStr() {
      this.signParam = '';
      if (this.havekey === true) {
        for (let i = 0; i < this.signSelect.length; i += 1) {
          this.signParam += `${this.signSelect[i].label}${this.midConn}${
            this.signSelect[i].variable
          }${this.lastConn}`;
        }
      } else if (this.havekey === false) {
        for (let i = 0; i < this.signSelect.length; i += 1) {
          this.signParam += `${this.midConn}${this.signSelect[i].variable}${
            this.lastConn
          }`;
        }
      }
      this.signParam = this.signParam.substring(
        0,
        this.signParam.length - this.lastConn.length,
      );
      if (this.checked) {
        this.signParam += `${this.keyConn}${this.keyName}${this.keyConn2}${
          this.keyType
        }`;
      }
      this.signIt();
    },
    signIt() {
      this.signArr = `
        {
          "name": "${this.form.name}",
          "amount_unit": "${this.form.amount_unit}",
          "method": "${this.form.method}",
          "orderNumber": "${this.form.orderNumber}",
          "successKey": "${this.form.successKey}",
          "successValue": "${this.form.successValue}",
          "signKey": "${this.form.signKey}",
          "msg": "${this.form.msg}",
      `;
      this.signArr += '"str_arr":[';
      for (let i = 0; i < this.signSelect.length; i += 1) {
        this.signArr += `"${this.signSelect[i].label}"
        ,`;
      }
      this.signArr = this.signArr.substring(0, this.signArr.length - 1);
      this.signArr += `],"mid_conn":"${this.midConn}","last_conn":"${
        this.lastConn
      }","encrypt":[`;
      for (let i = 0; i < this.format.length; i += 1) {
        this.signArr += `"${this.format[i]}",`;
      }
      this.signArr = this.signArr.substring(0, this.signArr.length - 1);
      this.signArr += ']';
      if (this.checked === true) {
        this.signArr += `,"key_str":"${this.keyConn}${this.keyName}${
          this.keyConn2
        }","key":"${this.keyType}"}`;
      } else {
        this.signArr += ',"key_str":""}';
      }
      const text2 = document.querySelector('#text2');
      text2.value = this.signArr;
      try {
        this.JSONstr = JSON.parse(JSON.stringify(this.signArr));
        console.log(this.JSONstr);
      } catch (error) {
        console.log(error);
      }
    },
    createPHP() {
      this.loading = true; // 停用按鈕
      this.$http
        .post('./php/jsontophp_notify.php', {
          data: JSON.stringify(this.JSONstr),
        })
        .then((res) => {
          console.log(res);
          // 建立隱藏的可下載連結
          const eleLink = document.createElement('a');
          eleLink.download = 'notify.php';
          eleLink.style.display = 'none';
          // 字元內容轉變成blob地址
          const blob = new Blob([res.data]);
          eleLink.href = URL.createObjectURL(blob);
          // 觸發點選
          document.body.appendChild(eleLink);
          eleLink.click();
          // 然後移除
          document.body.removeChild(eleLink);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
<style lang="scss">
