        align-items: center;
      }
    }
    #addparameter {
      width: 50%;
      margin-top: 50px;
      margin-bottom: 80px;
    }
  }
  #encryption {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
    table {
      text-align: center;
      margin-bottom: 30px;
      .is-circle {
        position: absolute;
        margin-left: 10px;
      }
    }
    .textArea {
      position: relative;
      .copyIcon {
        position: absolute;
        right: 4px;
        bottom: 10px;
        cursor: pointer;
      }
    }
  }
  textarea {
    resize: none;
    outline: none;
  }
}
</style>
