import Vue from 'vue';
import axios from 'axios';
import {
  Form,
  FormItem,
  Select,
  Option,
  Input,
  Button,
  Transfer,
  Dialog,
  Radio,
  Checkbox,
  Table,
  TableColumn,
} from 'element-ui';
import App from './App.vue';

Vue.use(Form);
Vue.use(FormItem);
Vue.use(Select);
Vue.use(Option);
Vue.use(Input);
Vue.use(Button);
Vue.use(Transfer);
Vue.use(Dialog);
Vue.use(Radio);
Vue.use(Checkbox);
Vue.use(Table);
Vue.use(TableColumn);

Vue.config.productionTip = false;

Vue.prototype.$http = axios;

new Vue({
  render: h => h(App),
}).$mount('#app');
